import React, { Component } from 'react'
import { Button } from 'reactstrap'
import { FaBars } from 'react-icons/fa'

// CSS
import './navBar.css'



export class NavBar extends Component {
  state = {
    isOpen: false,
    prevScrollpos: window.pageYOffset,
  }

  toggle = () => {
    this.setState({
      isOpen: !this.state.isOpen
    })
  }

  mobileNavLinks = () => {
    return (
      <div className='mobileNavBarMainLinks'>
        <a href="#clients" onClick={this.toggle}>Our Clients</a>
        <a href="#tools" onClick={this.toggle}>Tools</a>
        <a href="#about" onClick={this.toggle}>About Us</a>
        <a href="" onClick={this.toggle}>Contact</a>
      </div>
    )
  }

  render() {
    // setInterval(() => {
    //   console.log(this.state.prevScrollpos)
    // }, 100);
    return (
      <div className="navBarMainContainer" >

        <div className='standardNavBar'>
          <div className='navBarLogo'>
            <img src="https://previews.123rf.com/images/ikalvi/ikalvi1712/ikalvi171200300/92411491-mt-business-vector-icon-design-.jpg"></img>
          </div>

          <div className='navBarMainLinks'>
            <a href="#clients">Our Clients</a>
            <a href="#tools">Tools</a>
            <a href="#about">About Us</a>
          </div>

          <div className='navBarSideLinks'>
            <a>Contact</a>
          </div>
        </div>


        {this.state.isOpen ? (this.mobileNavLinks()) : (null)}

        <div className='mobileNavBarMenu' onClick={this.toggle}>
          <div className='mobileNavBarLogo'>
            <img src="https://previews.123rf.com/images/ikalvi/ikalvi1712/ikalvi171200300/92411491-mt-business-vector-icon-design-.jpg"></img>
          </div>
          <div className='mobileNavBarBars'>
            <FaBars />
          </div>
        </div>

      </div>
    )
  }
}

export default NavBar;
