import React from 'react'

// CSS
import './ourClients.css'

export default function OurClients() {
  return (
    <div className="ourClientsMainContainer" id="clients">

      <div className="ourClientsSubContainer">
        <div className='ourClientsInformation'>
          <span className="ourClientsInfoTitle">We provide the tools necessary for businesses to save on their bottom line.</span>
          <p className="ourClientsInfoRegText">With decades of industry knowledge coupled with sophisticated technology and algorithms, we provide the best solutions for your business.</p>
        </div>

        <div className='ourClientsImage'>
          <img src="http://getdrawings.com/free-icon/user-flat-icon-59.png"></img>
        </div>
      </div>

    </div>
  )
}
